.product-hero {
	padding-top: 50px;

	.breadcrumbs {
		margin-bottom: 70px;
	}

	&__slider {
		.slick-prev {
			display: none;
		}
	}

	&__left {
		width: 80%;
	}

	&__right {
		width: 20%;
	}

	&__big-img {
		background-color: #E8F0F2;
		min-height: 700px;
		display: flex !important;
		align-items: center;
		justify-content: center;
	}

	&__small-img {
		background-color: #E8F0F2;
		height: 220px;
		display: inline-flex !important;
		align-items: center;
		justify-content: center;
		margin-bottom: 20px;
		cursor: pointer;
	}

	&__slider-nav {

		.slick-track {
			display: flex;
			flex-direction: column;
		}

		.slick-slide {
			width: 220px !important;
		}
	}
}