.product-hero {
  padding-top: 50px;
}

.product-hero .breadcrumbs {
  margin-bottom: 70px;
}

.product-hero__slider .slick-prev {
  display: none;
}

.product-hero__left {
  width: 80%;
}

.product-hero__right {
  width: 20%;
}

.product-hero__big-img {
  background-color: #E8F0F2;
  min-height: 700px;
  display: flex !important;
  align-items: center;
  justify-content: center;
}

.product-hero__small-img {
  background-color: #E8F0F2;
  height: 220px;
  display: inline-flex !important;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
  cursor: pointer;
}

.product-hero__slider-nav .slick-track {
  display: flex;
  flex-direction: column;
}

.product-hero__slider-nav .slick-slide {
  width: 220px !important;
}
